.transTable {
  display: flex;
  justify-content: center;
}

table {
  font-family: "Lucida Sans Unicode", "Lucida Grande", Sans-Serif;
  font-size: 14px;
  margin-top: 30px;
  border-collapse: collapse;
  text-align: center;
  box-shadow: 0px 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 14%),
    0px 2px 1px rgb(0 0 0 / 20%);
}

th {
  width: 250px;
  height: 45px;
  padding: 10px 20px;
  border-style: solid;
  border-width: 0 1px 1px 0;
  text-align: center;
  font-weight: 600;
  color: #ffffff;
  border-color: #ffffff;
  background: #07aecc;
  text-transform: uppercase;
}

td {
  width: 250px;
  height: 45px;
  padding: 10px 20px;
  color: #4d4d4df1;
}

th,
td {
  border: 1px solid rgb(0, 0, 0);
}

tr:nth-child(2n) {
  background-color: #e7fffd;
}
